<template>
  <nav
    class="navbar navbar-expand-lg navbar-light bg-light border-bottom justify-content-between"
  >
    <button
      class="btn"
      @click="toogleMenu()"
      type="button"
      data-toggle="collapse"
      data-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span class="navbar-toggler-icon"></span>
    </button>
    <div>
      <ul class="list-group list-group-horizontal">

        <li class="list-group-item border-0 p-1 me-md-1">
          <router-link
            class="btn btn-danger"
            :to="{ name: 'Cart' }"
          >
            
            <i class="fas fa-shopping-cart"></i>
            سبد خرید
            <span class="badge bg-primary">
              {{ this.$store.state.cartCount }}
            </span>
          </router-link>
        </li>

        <li class="list-group-item border-0 pt-2 p-1 me-md-1">
          <a
            class="text-decoration-none text-secondary"
            href="#"
            @click="logout"
          >
            خروج
            <i class="fas fa-sign-out-alt"></i>
          </a>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import ArefApi from "../libs/api.js";

export default {
  name: "TheTopmenu",
  data() {
    return {
      menuToggle: this.$store.state.menuToggle,
      arefApi: new ArefApi(this),
    };
  },
  methods: {
    toogleMenu: function () {
      this.menuToggle = this.menuToggle == "toggled" ? "" : "toggled";
      this.$store.commit("set", ["menuToggle", this.menuToggle]);
    },
    logout: function () {
      this.arefApi.logout();
    },
  },
};
</script>
